#nav-men {
  display: flex;
  position: fixed;
  background-color: rgba(91, 6, 112, 0.3);
  padding: 0.7rem 1.7rem;
  z-index: 2;
  left: 50%;
  transform: translateX(-50%);
  bottom: 3rem;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
  display: none;

}

#nav-men a {
  background: transparent;
  padding: 1rem;
  border-radius: 50%;
  display: flex;
  color: white;
  font-size: 1.1rem;
}

#nav-men a:hover {
  background-color: rgba(157, 5, 5, 0.3);
}

#nav-men a#active {
  background-color: #AAC8A7;
}

@media all and (min-width: 768px) and (max-width: 1115px) {
  #nav-men {
    display: flex;
    position: fixed;
    background-color: rgba(91, 6, 112, 0.3);
    padding: 0.7rem 1.7rem;
    z-index: 2;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
  }
  #nav-men a {
    background: transparent;
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    color: white;
    font-size: 1.1rem;
  }

  #nav-men a:hover {
    background-color: rgba(91, 6, 112, 0.3);
  }

  #nav-men a#active {
    background-color: rgba(91, 6, 112, 0.3);
  }
}

@media all and (min-width: 320px) and (max-width: 767px) {
  #nav-men {
    display: flex;
    position: fixed;
    background-color: rgba(91, 6, 112, 0.3);
    padding: 0.7rem 1.7rem;
    z-index: 20;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    gap: 0px;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
  }
  #nav-men a {
    background: transparent;
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.1rem;
  }

  #nav-men a:hover {
    background-color: rgba(91, 6, 112, 0.3);
  }

